.mktoForm {
  padding: 0;
  max-width: 680px !important;
  width: 100% !important;
  font-family: 'Fira Sans', 'Trebuchet MS', 'Arial', sans-serif !important;

  /* Add your custom CSS below */

  fieldset {
    padding: 0 2rem 0;
  }

  .mktoGutter {
    float: none;
    height: 0 !important;
  }

  .mktoOffset {
    height: 0 !important;
  }

  .mktoFormCol {
    width: 100% !important;
  }

  .mktoFieldWrap {
    width: 100% !important;
    position: relative;
  }

  .mktoAsterix {
    color: #ed134b;
    display: inline-block;
  }

  input[type='text'],
  input[type='email'],
  input[type='tel'],
  select,
  textarea {
    font-size: 1rem !important;
    line-height: 1;
    padding: 1.5rem 0.75rem 0.5rem !important;
    font-family: 'Fira Sans', 'Trebuchet MS', 'Arial', sans-serif !important;
    color: #16214b !important;
    background-color: #f5f5f5 !important;
    border: 1px solid #9e9e9e !important;
    outline: none;
    width: 100% !important;
    max-width: 680px !important;
  }

  textarea {
    min-height: 150px;
  }

  label[for='confirminterest'] {
    top: 0 !important;
    padding: 0 !important;
  }

  .mktoLabel {
    float: none;
    line-height: 1.5;
    color: #999;
    font-size: 0.75rem;
    letter-spacing: 0.15px;
    position: absolute;
    top: 4px !important;
    left: 12px;
    width: auto !important;
    transition: all 0.1s ease-out;

    // &.mktoFieldFocused,
    // &.mktoFieldContainsData {
    //   top: 5%;
    //   font-size: 0.75rem;
    // }
  }
  .mktoForm .mktoRequiredField label.mktoLabel {
    font-weight: fw(regular);
  }

  input[type='text']:focus,
  input[type='email']:focus,
  input[type='tel']:focus,
  textarea:focus {
    border: 1px solid #16214b !important;
  }

  .mktoButtonRow {
    width: 100%;
    max-width: 680px;
    margin-top: 1rem;
  }

  .mktoButtonWrap {
    margin-left: 0 !important;
    width: 100%;
    max-width: 680px;
  }

  .mktoButton {
    border: 1px solid #ed134b !important;
    border-radius: 20px !important;
    height: 40px;
    font-size: 14px !important;
    font-weight: 700;
    line-height: 40px !important;
    letter-spacing: 0.15rem !important;
    padding: 0 45px !important;
    color: #111 !important;
    text-transform: uppercase;
    background-color: transparent !important;
    background-image: none !important;
    transition: color 0.3s ease-out;
  }

  .mktoButton:hover {
    color: #ed1b34 !important;
  }

  label[for='donotpostalmail'] {
    left: 24px;
  }

  .mktoCheckboxList {
    display: block;
    position: relative;
    user-select: none;
    padding: 0 !important;

    input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 20px;
      width: 20px;
      z-index: 10;

      &:checked ~ .checkmark:after {
        display: block;
      }
    }

    .checkmark {
      position: absolute;
      top: 0;
      left: 0;
      height: 18px;
      width: 18px;
      background-color: #fff;
      border: 2px solid #ccc;

      &:after {
        content: '';
        position: absolute;
        display: none;
        left: 5px;
        top: 1px;
        width: 5px;
        height: 10px;
        border: solid white;
        border-width: 0 2px 2px 0;
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
      }
    }
    &:hover input ~ .checkmark {
      background-color: #ccc;
    }

    input:checked ~ .checkmark {
      background-color: #111;
      border: solid 2px #111;
    }
  }
}

.cf_widget {
  background-color: $blue-50;
  padding: 2rem;
  border-radius: 4px;

  .cf_vote_holder {
    width: auto !important;
    margin: 0 !important;
  }

  .cf_header .cf_headline {
    font-family: 'Fira Sans', 'Trebuchet MS', Helvetica, sans-serif;
    text-align: left !important;
  }

  .cf_vote_item {
    display: flex;
    align-items: center;
    height: auto !important;
    border-color: color(borders) !important;
    background-image: none !important;
    background-color: $white !important;

    &.cf_first {
      border-top-left-radius: 4px !important;
      border-top-right-radius: 4px !important;
    }
    &.cf_last {
      border-bottom-left-radius: 4px !important;
      border-bottom-right-radius: 4px !important;
    }
  }

  .cf_vote_imgwrap {
    width: 40px !important;
    height: 40px !important;
    border: none !important;
    margin: 0 16px 0 8px !important;

    .cf_vote_img {
      background-size: 100%;
      width: 40px !important;
      height: 40px !important;
    }
  }

  .cf_vote_copy {
    flex-grow: 1;
    width: auto !important;

    .cf_vote_title {
      font-family: 'Fira Sans', 'Trebuchet MS', Helvetica, sans-serif;
      margin: 0 !important;
      font-size: 16px;
      letter-spacing: 0.25px;
      height: auto !important;
    }

    .cf_vote_count {
      img {
        filter: hue-rotate(100deg);
      }
    }

    .cf_vote_count_copy_percent {
      font-size: 18px;
      font-weight: fw(bold);
    }
  }

  .cf_vote_actionwrap {
    position: relative !important;
    right: auto !important;
    top: auto !important;
    margin-right: 0.5rem;

    .cf_vote_button {
      background-color: $blue-50 !important;
      background-image: none !important;
      border: none !important;
      color: $blue-700 !important;
      height: auto !important;
      padding: 0.5rem 1rem;
      transition: background-color 0.3s ease-out;

      &:hover {
        background-color: $blue-100 !important;
      }

      img {
        filter: hue-rotate(100deg);
      }
    }
  }

  .cf_footer {
    padding: 0 !important;
  }
}
