  left: 0;
  right: auto;
}

.flickity-prev-next-button:disabled {
  cursor: auto;
  opacity: 0.3;
}

.flickity-prev-next-button svg {
  height: 60%;
  width: 60%;
  left: 20%;
  position: absolute;
  top: 20%;
}

